.wp-block-blocks-gamestore-block-footer {
  margin-bottom: clamp(3.75rem, calc(2.77rem + 4.92vw), 7.5rem)!important;

  .inner-footer {
    border-radius: 8px;
    border: 1px solid var(--divider-main, rgba(14, 13, 15, 0.08));
    background: var(--background-2, rgba(250, 250, 250, 0.02));
    backdrop-filter: blur(12px);
    padding: 48px;
    display: flex;
    flex-direction: column;
    gap: 24px;
    box-sizing: border-box;

    .footer-copyrights {
      max-width: 400px;
      color: var(--text-secondary, rgba(14, 13, 15, 0.70));
      font-size: 16px;
      font-weight: 400;
      line-height: 1.5;
    }

    .footer-line {
      height: 1px;
      width: 100%;
      display: block;
      background: var(--divider-main, rgba(14, 13, 15, 0.08));
      margin-top: 24px;
    }
    .footer-bottom {
      display: flex;
      align-items: center;
      justify-content: space-between;
      gap: 24px;

      .left-part {
        display: flex;
        gap: 48px;
        align-items: center;
        color: var(--text-secondary, rgba(14, 13, 15, 0.70));
        font-size: 16px;
        font-weight: 600;

        .footer-logos {
          display: flex;
          align-items: center;
          gap: 8px;

          img.dark-logo {
            display: none;
          }
        }
      }
      .right-part {
        display: flex;
        align-items: center;
        justify-content: flex-end;
        gap:48px;

        a {
          color: var(--text-primary, #0E0D0F);
          font-size: 12px;
          font-weight: 700;
          line-height: 1;
          letter-spacing: 1.8px;
          text-transform: uppercase;
          text-decoration: none;
        }
      }
    }
  }
}
body.dark-mode-gamestore {
  .wp-block-blocks-gamestore-block-footer .inner-footer .footer-bottom .left-part .footer-logos img.dark-logo {
    display: block;
  }
  .wp-block-blocks-gamestore-block-footer .inner-footer .footer-bottom .left-part .footer-logos img.light-logo {
    display: none;
  }
}